// -----------------------------------------------------------------------------
// membuat event klik

//komponen klik yang akan di panggil pada komponen App
function Clicker(){
  // event clik
  function handleClick(e){
    alert("berhasil mengklik");
    e.preventDefault();
  }

  // membuat link yang didalamnya terdapat even
  return (
    <a href="#" onClick={handleClick}> klik di sini bro </a>
  )  
}
// -----------------------------------------------------------------------------


// -----------------------------------------------------------------------------
// event toggle

class Toggle extends Component{
  constructor(props) {
    super(props)
    this.state = {
      toggleStatus: true
    }

    this.handleClick = this.handleClick.bind(this)
  }

  handleClick(){
    this.setState(state => ({
      toggleStatus: !state.toggleStatus
    }))
  }

  render(){
    return(
      <button onClick={this.handleClick}>
        {this.state.toggleStatus ? 'ON' : 'OFF'}
        <p>{this.state.toggleStatus ? 'Kondisi Sekarang Menyala' : 'Kondisi Sekarang Mati'}</p>
      </button>
    )
  }
}
