// =============================================================================
// 9. react fragment sebagai pembungkus

// -----------------------------------------------------------------------------
class App extends Component{
  render(){
    return(
      <div>
        <table border='1'>
          <tr>
            <Column />
          </tr>
        </table>
      </div>
    );
  }
}

class Column extends React.Component{
  render(){
    return(
      // React.Fragment = untuk menghilangkan tag div yang tampil pada html
      <React.Fragment>
        <td>Data Satu</td>
        <td>Data Berikutnya</td>
      </React.Fragment>
    );
  }
}
